@import "../styles/variables.scss";

.range {
	background-color: transparent;
	color: $colorLight;
	padding: 5px 15px;
	border: 1px solid $colorLight;
	border-radius: 10px;

	& > * {
		background-color: transparent;
	}
}

.label {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-bottom: 5px;
	pointer-events: none;
}

.label:hover .slider {
	outline: none;
}

.slider {
	-webkit-appearance: none;
	width: 100%;
	height: 16px;
	background: transparent;
	cursor: pointer;

	&::-webkit-slider-thumb {
		-webkit-appearance: none;
		height: 14px;
		width: 14px;
		border: 1px solid $colorLight;
		border-radius: 100%;
		background-color: $colorLight;
		margin-top: -6px;
	}

	&::-moz-range-thumb {
		height: 14px;
		width: 14px;
		border: 1px solid $colorLight;
		border-radius: 100%;
		background-color: $colorLight;
	}

	&::-webkit-slider-runnable-track {
		width: 100%;
		height: 2px;
		background: $colorLight;
		border-radius: 2px;
	}

	&::-moz-range-track {
		width: 100%;
		height: 2px;
		background: $colorLight;
		border-radius: 2px;
	}

	&:hover,
	&:focus {
		outline: 1px dotted $colorLight;
	}
}
